
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import Calendar from 'vux/src/components/calendar/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'

export default {
components: {
XInput,PopupHeader,
XButton,
Group,
Cell,
Checklist,
Calendar
},
data () {
return {
f:[],
dingwei:'正在定位...',
commonList: [
'早上(07:00 - 11:00)',
'中午(11:00 - 15:00)',
'晚上(15:00 - 19:30)',
'夜晚(19:30 - 23:00)'
]
}
},
created(){
let _ = this;
setTimeout(()=>{
_.dingwei = "正在定位."
},700)
setTimeout(()=>{
_.dingwei = "正在定位.."
},1400)
setTimeout(()=>{
_.dingwei = "正在定位..."
},2100)
setTimeout(()=>{
_.dingwei = "广东石油化工学院"
},3000)
},
methods: {
comfirm(){
const _this = this 
if(!(_this.f.xm && _this.f.xb && _this.f.dh && _this.f.d && _this.f.t.length)){
this.$vux.alert.show({
title: '操作失败',
content: '你输入的信息不完整',
})
return;
}
this.$vux.confirm.show({
title: '确定提交？',
content: _this.f.xm + "　同学你好，请问是否提交？",
onCancel () {

},
onConfirm () {
_this.$vux.loading.show({
text: 'Loading'
})
setTimeout(() => {
_this.$vux.loading.hide()
_this.$router.push("/success")
}, 1500)
}
})
}
}
}



